.animated-bg-canvas.light{
    position: fixed;
    inset: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    display: block;
    pointer-events: none;
    image-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -mox-osx-font-smoothing: grayscale;
}

.animated-bg-canvas.dark{
    position: fixed;
    inset: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    display: block;
    pointer-events: none;
    image-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -mox-osx-font-smoothing: grayscale;
}


